import React, { useContext } from 'react';
import CustomInput from 'components/CustomInput';
import { CursorContext } from 'context/CursorContextProvider';

const Home = () => {
  const [, setCursor] = useContext(CursorContext);

  const handleChange = (event) => {
    if (event.target.id.includes('smoothness')) {
      const newId = event.target.id.split('.');
      setCursor((prevCursor) => ({
        ...prevCursor,
        smoothness: {
          ...prevCursor.smoothness,
          [newId[1]]: event.target.value,
        },
      }));
    }
    setCursor((prevCursor) => ({
      ...prevCursor,
      [event.target.id]: event.hex || event.target.value,
    }));
  };

  return (
    <>
      <section className='section'>
        <div className='container'>
          <h1 className='is-size-1 has-text-white has-text-weight-bold has-text-centered'>Custom React Cursor ðŸŽ‰</h1>
          <h2 className='subtitle has-text-centered mt-2 is-family-code has-text-grey-dark'>Animated, interactive and customizable cursor component</h2>
          <div className='is-flex full-width is-align-items-center is-justify-content-center mt-6'>
            <button type='button' className='button is-warning _button has-text-weight-semibold mr-5'>Hover over me!</button>
            <a href='https://github.com/ajmnz/custom-cursor-react' target='_blank' rel='noreferrer' className='underline has-text-success has-text-weight-bold _button'>Fork me on Github! ðŸŽ‰</a>
          </div>
        </div>
      </section>
      <section className='section'>
        <div className='container'>
          <div className='is-flex is-align-items-center is-justify-content-center'>
            <p className='is-family-code has-text-grey-light has-text-weight-bold is-size-4 has-text-centered _button'>Customize it!</p>
          </div>
          <div className='columns mt-5 is-centered'>
            <div className='column is-8'>
              <div className='columns'>
                <div className='column'>
                  <CustomInput
                    type='range'
                    id='dimensions'
                    step={1}
                    min={1}
                    max={100}
                    title='Dimensions'
                    handleChange={handleChange}
                  />
                </div>
                <div className='column'>
                  <CustomInput
                    type='range'
                    id='opacity'
                    step={0.1}
                    min={0}
                    max={1}
                    title='Opacity'
                    handleChange={handleChange}
                  />
                </div>
                <div className='column'>
                  <CustomInput
                    type='range'
                    id='targetOpacity'
                    step={0.1}
                    min={0}
                    max={1}
                    title='Hover Opacity'
                    handleChange={handleChange}
                  />
                </div>
                <div className='column'>
                  <CustomInput
                    type='range'
                    id='targetScale'
                    step={0.5}
                    min={0}
                    max={10}
                    title='Hover Scale'
                    handleChange={handleChange}
                  />
                </div>
              </div>
              <div className='is-flex is-align-items-center is-justify-content-center'>
                <p className='has-text-centered has-text-grey-light is-size-5 mt-5 mb-6 has-text-weight-bold underline _button'>Appearence</p>
              </div>
              <div className='columns is-centered'>
                <div className='column'>
                  <CustomInput
                    type='color'
                    id='fill'
                    title='Fill color'
                    handleChange={handleChange}
                  />
                </div>
                <div className='column'>
                  <CustomInput
                    type='color'
                    id='strokeColor'
                    title='Stroke Color'
                    handleChange={handleChange}
                  />
                </div>
                <div className='column'>
                  <CustomInput
                    type='range'
                    id='strokeWidth'
                    step={1}
                    min={0}
                    max={50}
                    title='Stroke Width'
                    handleChange={handleChange}
                  />
                </div>
              </div>
              <div className='is-flex is-align-items-center is-justify-content-center'>
                <p className='has-text-centered has-text-grey-light is-size-5 mt-5 mb-5 has-text-weight-bold underline _button'>Smoothness</p>
              </div>
              <div className='columns is-centered'>
                <div className='column'>
                  <CustomInput
                    type='range'
                    id='smoothness.movement'
                    step={0.01}
                    min={0}
                    max={1}
                    title='Movement'
                    handleChange={handleChange}
                  />
                </div>
                <div className='column'>
                  <CustomInput
                    type='range'
                    id='smoothness.scale'
                    step={0.01}
                    min={0}
                    max={1}
                    title='Scale'
                    handleChange={handleChange}
                  />
                </div>
                <div className='column'>
                  <CustomInput
                    type='range'
                    id='smoothness.opacity'
                    step={0.01}
                    min={0}
                    max={1}
                    title='Opacity'
                    handleChange={handleChange}
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </>
  );
};

export default Home;
