import React, { useContext, useEffect, useRef } from 'react';

import { CursorContext } from 'context/CursorContextProvider';
import Cursor from './Cursor';
import {
  addHoverEvent, handleWindowEvent,
  removeHoverEvent, removeWindowEvent,
} from './utils';

import './CustomCursor.css';

const CustomCursor = () => {
  const ref = useRef(null);
  const [cursor] = useContext(CursorContext);

  useEffect(() => {
    if (!ref) return null;

    const cursorClass = new Cursor(
      ref.current,
      cursor.smoothness,
      cursor.opacity,
      cursor.targetOpacity,
      cursor.targetScale,
    );

    addHoverEvent(cursorClass, cursor.targets);
    handleWindowEvent(cursorClass);

    return () => {
      removeHoverEvent(cursorClass, cursor.targets);
      removeWindowEvent(cursorClass);
      cursorClass.destroy();
    };
  }, [cursor]);

  return (
    <div className='cursor'>
      <svg
        ref={ref}
        className={cursor.customClass}
        height={cursor.dimensions}
        width={cursor.dimensions}
        viewBox={`0 0 ${cursor.dimensions} ${cursor.dimensions}`}
      >
        <circle
          cx={cursor.dimensions / 2}
          cy={cursor.dimensions / 2}
          r={(cursor.dimensions / 2) / 2}
          fill={cursor.fill}
          stroke={cursor.strokeColor}
          strokeWidth={cursor.strokeWidth}
        />
      </svg>
    </div>
  );
};

export default CustomCursor;
