/* eslint-disable react/require-default-props */
import React, { useContext, useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { TwitterPicker } from 'react-color';
import { CursorContext } from '../../context/CursorContextProvider';

const CustomInput = ({
  type,
  step,
  min,
  max,
  title,
  handleChange,
  id,
}) => {
  const [cursor] = useContext(CursorContext);
  const [value, setValue] = useState(cursor[id]);
  useEffect(() => {
    if (id.includes('smoothness')) {
      const newId = id.split('.');
      setValue(cursor.smoothness[newId[1]]);
    }
  }, [cursor.smoothness, id]);

  const handleOnChange = (event) => {
    if (event.hex) {
      setValue(event.hex);
      const fEvent = event;
      fEvent.target = {
        id,
      };
      handleChange(fEvent);
    } else {
      setValue(event.target.value);
      handleChange(event);
    }
  };

  return (
    <>
      <p className='has-text-grey-light'>{title}</p>
      {type === 'range'
        ? <input id={id} className='slider is-fullwidth' step={step} min={min} max={max} value={value} onChange={handleOnChange} type='range' />
        : (
          <TwitterPicker
            colors={['transparent', '#FF6900', '#FCB900', '#00D084', '#8ED1FC', '#0693E3', '#ABB8C3']}
            triangle='hide'
            color={value}
            onChange={handleOnChange}
          />
        )}
      <p className='has-text-centered'>
        {
          id.includes('smoothness')
            ? cursor.smoothness[id.replace('smoothness.', '')]
            : cursor[id]
        }
      </p>
    </>
  );
};

CustomInput.propTypes = {
  type: PropTypes.string,
  step: PropTypes.number,
  min: PropTypes.number,
  max: PropTypes.number,
  title: PropTypes.string,
  handleChange: PropTypes.func,
  id: PropTypes.string,
};

export default CustomInput;
